.content {
    .img {
        width: 200px;
        height: 200px;
        animation: rotateAnimation 0.5s infinite linear;
    }

    @keyframes rotateAnimation {
        0% {
            transform: rotate(0deg);
        }

        25% {
            transform: rotate(30deg);
        }

        75% {
            transform: rotate(-30deg);
        }

        100% {
            transform: rotate(0deg);
        }
    }

    .content-if {
        margin-top: -45px;
    }

    position: absolute;
    top: 50%;
    left: 50%;
    color: white;
    text-align: center;
    font-size: 30px;
    font-weight: 400;
    text-shadow: 2px 4px #ff0000;
    transform: translateX(-50%) translateY(-20%);

    .ten {
        font-weight: 600;
    }
}

@font-face {
    font-family: 'ExternalFont';
    src: url('../../public/Christmas\ Glitter\ ttf.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'ExternalFont1';
    src: url('../../public/SHPinscher-Regular.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}